import React, { useRef, useState } from 'react';
import { Text, Group, Button, useMantineTheme, LoadingOverlay} from '@mantine/core';
import { Dropzone } from '@mantine/dropzone';
import { IconCloudUpload, IconX, IconRefresh, IconDownload } from '@tabler/icons-react';
import styles from './DropzoneButton.module.css';

import dayjs from 'dayjs';
import utc from 'dayjs/plugin/utc';
import timezone from 'dayjs/plugin/timezone';

dayjs.extend(utc);
dayjs.extend(timezone);


function DropzoneButton() {
  const theme = useMantineTheme();
  const openRef = useRef();
  const [files, setFiles] = useState([]);
  const [loading, setLoading] = useState(false);

  const handleDrop = (acceptedFiles) => {
    setFiles(currentFiles => [...currentFiles, ...acceptedFiles]);
  };


  function parseTime(timeStr) {
    if (!timeStr || timeStr.toUpperCase() === 'NULL') {
      return 0; //  0,     NULL
    }
  
    let totalSeconds = 0;
    const parts = timeStr.split(':');
    const secondsParts = parts[parts.length - 1].split('.');
  
    //     (, )
    if (parts.length === 3) {
      totalSeconds += parseInt(parts[0]) * 3600; // 
      totalSeconds += parseInt(parts[1]) * 60;   // 
    } else if (parts.length === 2) {
      totalSeconds += parseInt(parts[0]) * 60;   // 
    }
  
    //  
    totalSeconds += parseInt(secondsParts[0]);  // 
  
    //  ,   1000,    
    if (secondsParts.length > 1) {
      totalSeconds += parseFloat('0.' + secondsParts[1]);
    }
  
    return totalSeconds;
  }
  
  function formatTime(seconds) {
    //         
    const roundedSeconds = Math.round(seconds);
    const hours = Math.floor(roundedSeconds / 3600);
    const minutes = Math.floor((roundedSeconds % 3600) / 60);
    const secs = roundedSeconds % 60;
  
    return [hours, minutes, secs].map(v => v.toString().padStart(2, '0')).join(':');
  }
  
  function getMonthName(monthNumber) {
    const monthNames = [
      "", "", "", "", "", "",
      "", "", "", "", "", ""
    ];
    return monthNames[monthNumber];
  }
  
  
  
  


  const handleTransform = async () => {
    if (files.length < 2) {
      alert("Please upload at least two JSON files for merging.");
      return;
    }

    setLoading(true);
  
    const dataPromises = files.map(file =>
      new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = () => {
          try {
            resolve(JSON.parse(reader.result));
          } catch (e) {
            reject(e);
          }
        };
        reader.onerror = () => reject(reader.error);
        reader.readAsText(file);
      })
    );
  
    try {
      const results = await Promise.all(dataPromises);
      const mergedData = results.reduce((acc, data) => [...acc, ...data], []);
  
      const statAuditlog = {};
      const statTimetracker = {};
      const dailyWork = {};
      const thresholdSeconds = (4 * 3600) + (45 * 60); // 4   45   
  
      mergedData.forEach(item => {
        const isTimetracker = item.source_table === 'timetracker';
        const seconds = parseTime(item.time_spent);
  
        switch (item.source_table) {
          case 'auditlog':
            //    auditlog
            const auditStats = statAuditlog[item.user_name] = statAuditlog[item.user_name] || {
              task_count: 0, task_time: 0, complete: 0, not_complete: 0, missed: 0, closed_by_timeout: 0, not_closed: 0,
              not_complete_tasks: {
                PreFilterPictures: 0,
                ImageChoice: 0,
                ImageBackgroundRemoval: 0,
                ProductModeration: 0,
                DoubtfulModeration: 0,
                ProductApproval: 0,
                UpdatePictureFilter: 0,
                ImageUpdate: 0,
                ProductUpdate: 0
              },
              monthCounter: {},
              yearCounter: {}
            };
            auditStats.task_count += 1;
            auditStats.task_time += seconds;
            if (item.is_complete === "true") {
              auditStats.complete += 1;
            } else if (item.is_complete === "false" && item.is_missed === "false") {
              auditStats.not_complete += 1;
              if (auditStats.not_complete_tasks[item.task_type] !== undefined) {
                auditStats.not_complete_tasks[item.task_type] += 1;
              }
            }
            if (item.is_missed === "true") {
              auditStats.missed += 1;
            }
  
            if (item.finish_note === "Closed by timeout") {
              auditStats.closed_by_timeout += 1;
            } else if (item.finish_note === "Not Closed") {
              auditStats.not_closed += 1;
            }
  
            //      start_time
            const [date] = item.start_time.split(' ');
            const [year, month] = date.split('-');
            if (!auditStats.monthCounter[month]) {
              auditStats.monthCounter[month] = 0;
            }
            if (!auditStats.yearCounter[year]) {
              auditStats.yearCounter[year] = 0;
            }
            auditStats.monthCounter[month] += 1;
            auditStats.yearCounter[year] += 1;
            break;
  
          case 'timetracker':
            //    timetracker
            const trackerStats = statTimetracker[item.user_name] = statTimetracker[item.user_name] || {
              task_count: 0, task_custom_count: 0, task_complex_count: 0, custom_time: 0, complex_time: 0, task_time: 0,
              complete: 0, not_complete: 0, missed: 0, underworked_days: 0
            };
            trackerStats.task_count += 1;
            if (item.task_type === 'Custom') {
              trackerStats.task_custom_count += 1;
              trackerStats.custom_time += seconds;
            } else if (item.task_type === 'Complex') {
              trackerStats.task_complex_count += 1;
              trackerStats.complex_time += seconds;
            } else {
              trackerStats.task_time += seconds;
            }
  
            if (item.is_complete === "true") {
              trackerStats.complete += 1;
            } else if (item.is_complete === "false" && item.is_missed === "false") {
              trackerStats.not_complete += 1;
            }
            if (item.is_missed === "true") {
              trackerStats.missed += 1;
            }
  
            //        
            const trackerDate = dayjs.utc(item.start_time).tz('Europe/Kiev').format('YYYY-MM-DD');
            if (item.user_name === "AleksandrKolesnikov") {
              // console.log(`User: ${item.user_name}, Start Time: ${item.start_time}, Converted Date: ${trackerDate}`);
            }
            if (!dailyWork[item.user_name]) {
              dailyWork[item.user_name] = {};
            }
            if (!dailyWork[item.user_name][trackerDate]) {
              dailyWork[item.user_name][trackerDate] = 0;
            }
            dailyWork[item.user_name][trackerDate] += seconds;
            break;

          default:
            // console.warn(`Unknown source_table: ${item.source_table}`);
        }
      });

      //   
      Object.keys(dailyWork).forEach(user_name => {
        Object.keys(dailyWork[user_name]).forEach(date => {
          if (dailyWork[user_name][date] < thresholdSeconds) {
            if (!statTimetracker[user_name].underworked_days) {
              statTimetracker[user_name].underworked_days = 0;
            }
            statTimetracker[user_name].underworked_days += 1;
          }
        });
      });


      //     
      const allDates = mergedData.map(item => dayjs.utc(item.start_time).tz('Europe/Kiev'));
      const minDate = allDates.reduce((a, b) => a.isBefore(b) ? a : b);
      const maxDate = allDates.reduce((a, b) => a.isAfter(b) ? a : b);

      console.log("Max Date from Data:", maxDate.format('YYYY-MM-DD'));  //     

      //  
      let period = '';
      if (minDate.date() <= 10) {
        period = '01-10';
      } else if (minDate.date() <= 20) {
        period = '11-20';
      } else {
        // ,     maxDate,      
        period = `21-${maxDate.date()}`;
      }

      const fileName = `${minDate.year()}-${minDate.format('MMM').toUpperCase()}_${period}`;





      //        
      function getMostFrequent(counter) {
        return Object.entries(counter).reduce((a, b) => (b[1] > a[1] ? b : a))[0];
      }
  
      //       
      const finalData = [...mergedData];
      Object.entries(statAuditlog).forEach(([user_name, data]) => {
        data.not_complete_percentage = (data.not_complete / data.task_count * 100).toFixed(2);
        const mostFrequentMonth = getMonthName(Number(getMostFrequent(data.monthCounter)) - 1);
        const mostFrequentYear = getMostFrequent(data.yearCounter);
        delete data.monthCounter; //   
        delete data.yearCounter; //   
        finalData.push({
          source_table: "stat_auditlog",
          user_name,
          ...data,
          month: mostFrequentMonth,
          year: mostFrequentYear,
          task_time: formatTime(data.task_time)
        });
      });
      Object.entries(statTimetracker).forEach(([user_name, data]) => {
        data.not_complete_percentage = (data.not_complete / data.task_count * 100).toFixed(2);
        finalData.push({
          source_table: "stat_timetracker",
          user_name,
          ...data,
          custom_time: formatTime(data.custom_time),
          complex_time: formatTime(data.complex_time),
          task_time: formatTime(data.task_time)
        });
      });
  
      const finalJson = JSON.stringify(finalData, null, 2);
      downloadJson(finalJson, `${fileName}.json`);
  
    } catch (error) {
      console.error("Error reading or merging files:", error);
    }
  };
  
  
  
  
  //    
  const downloadJson = (jsonString, fileName) => {
    const blob = new Blob([jsonString], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = fileName;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
    setLoading(false);
  };
  
  

  const handleReset = () => {
    setFiles([]); //   
  };

  return (
    <div className={styles.wrapper}>
      <LoadingOverlay visible={loading} />
      <Dropzone
        openRef={openRef}
        onDrop={handleDrop}
        className={styles.dropzone}
        radius="md"
        accept={['application/json']} //   JSON 
        maxSize={30 * 1024 ** 2} //    30MB
        multiple  //     
      >
        <div style={{ pointerEvents: 'none' }}>
          <Group justify="center">
            <Dropzone.Accept>
              <IconDownload
                style={{ width: 50, height: 50 }}
                color={theme.colors.blue[6]}
                stroke={1.5}
              />
            </Dropzone.Accept>
            <Dropzone.Reject>
              <IconX
                style={{ width: 50, height: 50 }}
                color={theme.colors.red[6]}
                stroke={1.5}
              />
            </Dropzone.Reject>
            <Dropzone.Idle>
              <IconCloudUpload
                style={{ width: 50, height: 50 }}
                stroke={1.5}
              />
            </Dropzone.Idle>
          </Group>

          <Text ta="center" fw={700} fz="lg" mt="xl">
            <Dropzone.Accept>Drop files here</Dropzone.Accept>
            <Dropzone.Reject>Only JSON files less than 30MB</Dropzone.Reject>
            <Dropzone.Idle>Upload JSON files</Dropzone.Idle>
          </Text>
          <Text ta="center" fz="sm" mt="xs" c="dimmed">
            Drag'n'drop files here, or click to select files.
          </Text>
        </div>
      </Dropzone>

      {files.length > 0 && (
        <div className={styles.fileList}>
          {files.map((file, index) => (
            <Text className={styles.file_badge} key={index}>{file.name}</Text>
          ))}
        </div>
      )}
      <div className={styles.controls_button}>
        <Button
          className={styles.control}
          size="md"
          radius="xl"
          onClick={handleTransform}
        >
          Download
        </Button>
        <Button
          className={styles.control}
          size="md"
          radius="xl"
          color="red"
          onClick={handleReset}
        ><IconRefresh stroke={2} />
        </Button>
      </div>
    </div>
  );
}

export default DropzoneButton;
